<template>
    <div>
        <Header/>
            <div class="container">
                <br><br>
                <h2>Registro</h2>
                <form>
                    <div class="form-group">
                        <input class="form-control form-control-lg" type="text" placeholder="Name" v-model="form.name">
                    </div>
                    <div class="form-group">
                        <input class="form-control form-control-lg" type="text" placeholder="Job" v-model="form.job">
                    </div>
                    <br>
                    <button type="button" class="btn btn-success" v-on:click="guardar()">Guardar</button>
                    <button type="button" class="btn btn-danger" v-on:click="salir()">Salir</button>
                </form>
            </div>
        <Footer/>
    </div>
</template>
<script>

import Header from '@/components/HeaderPage.vue'
import Footer from '@/components/FooterPage.vue'
import axios from 'axios'


export default {
    name:"NewPage",
    components:{
        Header,
        Footer
    },
    data:function(){
        return{
            form:{
                "name": "",
                "job": ""
            }
        }
    },
    methods:{
        salir(){
            this.$router.push("/firstpage")
        },
        guardar(){
            console.log("guardando")
            //console.log(this.form)
            axios.post('https://reqres.in/api/users',this.form).then(datos=>{
                console.log(datos)
                this.makeToast("Hecho","usuario creado","success")
                setTimeout(() => {
                    this.$router.push("/firstpage")
                }, 3000);
            }).catch(e => {
                console.log(e)
                this.makeToast("ERROR","usuario no creado","warning")
            })
        },
        makeToast(titulo,texto,tipo) {
            this.toastCount++
            this.$bvToast.toast(texto, {
            title: titulo,
            variant:tipo,
            autoHideDelay: 3000,
            appendToast: true
            })
        }
    }
}
</script>
