<template>
    <div>
        <Header />
        <br>
        <div class="container">
            <form action="#">
                <div class="form-group left">
                    <label for="id" >ID</label>
                    <input type="text" class="form-control" id="id" placeholder="ID" v-model="form.id" disabled>
                </div>
                <div class="form-group left">
                    <label for="email" >email</label>
                    <input type="text" class="form-control" id="email" placeholder="email" v-model="form.email">
                </div>
                <div class="form-group left">
                    <label for="first_name" >first_name</label>
                    <input type="text" class="form-control" id="first_name" placeholder="first_name" v-model="form.first_name">
                </div>
                <div class="form-group left">
                    <label for="last_name" >last_name</label>
                    <input type="text" class="form-control" id="last_name" placeholder="last_name" v-model="form.last_name">
                </div>
                <div class="form-group left">
                    <label for="avatar" >avatar</label>
                    <input type="text" class="form-control" id="avatar" placeholder="avatar" v-model="form.avatar">
                </div>
                <br>
                <button type="button" class="btn btn-primary" v-on:click="editar()">Editar</button>
                <button type="button" class="btn btn-warning" v-on:click="eliminar()">Eliminar</button>
                <button type="button" class="btn btn-danger" v-on:click="salir()">Salir</button>
            </form>
        </div>

        <Footer />
    </div>
</template>
<script>
import Header from '@/components/HeaderPage.vue'
import Footer from '@/components/FooterPage.vue'
import axios from 'axios'

export default {
    name:"EditPage",
    components:{
        Header,
        Footer
    },data:function(){
        return {
            userId:null,
            datosform:{
                "name": "Everrr",
                "job": "zion resident"
            },
            form:{
                "id":"",
                "email":"",
                "first_name":"",
                "last_name":"",
                "avatar":""
            }
        }
    },methods:{
        editar(){
            console.log("editando")
            //console.log(this.datosform)

            axios.patch('https://reqres.in/api/users/'+this.userId,this.form).then(datos => {
                console.log(datos)
            })
        },
        salir(){
            this.$router.push("/firstpage")
        },
        eliminar(){
            console.log("eliminando")
            let eliminarurl = 'https://reqres.in/api/users/'+this.form.id
            axios.delete(eliminarurl).then(datos =>{
                console.log(datos)
                setTimeout(() => {
                    this.$router.push("/firstpage")
                }, 3000);
            })
        }
    },
    mounted:function() {
        this.userId = this.$route.params.id;
        this.form.id = this.$route.params.id;
        //console.log(this.userId)
        axios.get('https://reqres.in/api/users/'+this.userId).then(datos=>{
            console.log(datos)
            this.form.id = datos.data.data.id
            //console.log(this.form.id)
            this.form.email = datos.data.data.email
            this.form.first_name = datos.data.data.first_name
            this.form.last_name = datos.data.data.last_name
            this.form.avatar = datos.data.data.avatar
            //console.log(this.form)
        })
    }
}
</script>
<style scoped>
    .left{
        text-align: left;
    }
</style>