<template>
    <div>
        <Header/> 
        <br><br>   
        <div class="container left">

            <div class="btn btn-primary" v-on:click="nuevo()">Nuevo + </div>    
            <br/>
            <table class="table">
                <thead class="thead-light">
                    <tr>
                    <th scope="col">#</th>
                    <th scope="col">Email</th>
                    <th scope="col">Nombre</th>
                    <th scope="col">Apellido</th>
                    <th scope="col">Imagen</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="usuario in Listausuarios" :key="usuario.id" v-on:click="editar(usuario.id)">
                        <td>{{usuario.id}}</td>
                        <td>{{usuario.email}}</td>
                        <td>{{usuario.first_name}}</td>
                        <td>{{usuario.last_name}}</td>
                        <td>{{usuario.avatar}}</td>
                    </tr>
                </tbody>
            </table>
        </div>
        <Footer/>
    </div>
</template>

<script>
    import Header from '@/components/HeaderPage.vue'
    import Footer from '@/components/FooterPage.vue'
    import axios from 'axios'

    export default {
        name: 'FirstPage',
        data(){
            return{
                Listausuarios:null,
                pagina:1
            }
        },
        components:{
            Header,
            Footer
        },
        methods:{
            editar(id){
                //console.log(id)
                this.$router.push('/editpage/'+id)
            },
            nuevo(){
                this.$router.push('/newpage')
            }
        },
        mounted:function () {
            let url = 'https://reqres.in/api/users?page='+this.pagina;
            axios.get(url).then(data=>{
                this.Listausuarios = data.data.data
            }); 
        }
    }
</script>

<style  scoped>
    .left{
        text-align: left;
    }

</style>